@mixin box-shadow($top, $left, $blur, $color, $inset: false) {
    @if $inset {
        -webkit-box-shadow: inset $top $left $blur $color;
        -moz-box-shadow: inset $top $left $blur $color;
        box-shadow: inset $top $left $blur $color;
    } @else {
        -webkit-box-shadow: $top $left $blur $color;
        -moz-box-shadow: $top $left $blur $color;
        box-shadow: $top $left $blur $color;
    }
}

@mixin regular-font($font-size) {
    font-family: edmondsansregular;
    font-size: $font-size;

}

@mixin medium-font($font-size) {
    font-family: edmondsansbold;
    font-size: $font-size;
}

@mixin bold-font($font-size) {
    font-family: edmondsansmedium;
    font-size: $font-size;
}

@mixin transition($time) {
    -webkit-transition: all $time; // Chrome
    -moz-transition: all $time; // Mozilla
    -o-transition: all $time; // Opera
    transition: all $time;
}

@mixin transparent-background-color-mixin($alpha, $color) {
    background-color: rgba(red($color), green($color), blue($color), $alpha);
}

@mixin box-sizing($box-model) {
    -webkit-box-sizing: $box-model; // Safari <= 5
    -moz-box-sizing: $box-model; // Firefox <= 19
    box-sizing: $box-model;
}

@mixin background-clip($model) {
    -moz-background-clip: $model;     // Firefox 3.6
    -webkit-background-clip: $model;  // Safari 4? Chrome 6?
    background-clip: $model + '-box';  // Firefox 4, Safari 5, Opera 10, IE 9
}

@mixin linear-gradient($start-color, $end-color) {
    background: $black; // default
    background-image: -webkit-radial-gradient(to left, $start-color, $end-color);
    background-image: -moz-radial-gradient(to left, $start-color, $end-color);
    background-image: -o-radial-gradient(to left, $start-color, $end-color);
    background-image: linear-gradient(to left, $start-color, $end-color);

}

@mixin filter($filter-type, $filter-amount) {
    -webkit-filter: $filter-type + unquote('(#{$filter-amount})');
    -moz-filter: $filter-type + unquote('(#{$filter-amount})');
    -ms-filter: $filter-type + unquote('(#{$filter-amount})');
    -o-filter: $filter-type + unquote('(#{$filter-amount})');
    filter: $filter-type + unquote('(#{$filter-amount})');
}

@mixin multiple-filters($filters) {
    -webkit-filter: $filters;
    -moz-filter: $filters;
    -ms-filter: $filters;
    -o-filter: $filters;
    filter: $filters;
}

@mixin basic-button($color) {
    background-repeat: no-repeat;
    background-color: $color;
    border: 0;
    cursor: pointer;
    overflow: hidden;
    outline: none;
    padding: 0;
}
